<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">系统管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">数据集成</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">流程步骤</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody1">
          <Form
            ref="formValidate"
            :model="formValidate"
            :rules="ruleValidate"
            :label-width="120"
            inline
          >
            <Row>
              <FormItem label="任务编号："  prop="stepNum">
                <Input v-model="formValidate.stepNum" placeholder="请输入任务编号" style="width:200px"></Input>
              </FormItem>
              <FormItem label="执行状态："  prop="status">
                <Select v-model="formValidate.status" style="width:200px">
                  <Option value="">请选择</Option>
                  <Option
                    :value="item.code"
                    v-for="(item) in stepNameList"
                    :key="item.code"
                  >{{item.name}}</Option>
                </Select>
              </FormItem>
              
              <Row>
                <div class="btnClass">
                  <Button
                    type="primary"
                    @click="handleSubmit(0, 1)"
                  >查询</Button>
                </div>
              </Row>
            </Row>
          </Form>
          <div class="contentCellBody">
            <div class="contentCellTitle">
              <div class="contentCellTitle-1"></div>数据列表
            </div>
            <Row>
              <table class="tableBox">
                <thead>
                  <tr>
                    <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                  </tr>
                </thead>
                <tbody>
                  <template>
                    <tr v-for="(item,index) in tableBody" :id="item.documentId">
                      <td>
                        {{pageSize*(pageNum-1)+index+1}}
                      </td>
                      <td class="noWrap">
                        <button class="buttonCls spanBox" :disabled="item.status=='1'" @click="operate('4',item)">强制更改</button>
                        <button class="buttonCls spanBox" :disabled="item.status=='1'" @click="operate('5',item)">重新执行</button>
                      </td>
                      <td>{{item.step_num}}</td>
                      <td>{{item.step_create_time}}</td>
                      <td>{{item.date_from}}</td>
                      <td>{{item.date_to}}</td>
                      <td>{{mapStatus[item.status]}}</td>
                      <td>{{item.exec_time1}}</td>
                      <td>{{item.exec_time2}}</td>
                    </tr> 
                  </template>
                </tbody>
              </table>
            </Row>
          </div>
          <div class="flexCenter pageBox">
            <Page :total="rowCount" 
            :page-size="pageSize" @on-change="pageChange" @on-page-size-change='pageSizefun'
            :current.sync="currentpage"
            show-elevator show-sizer></Page>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  mounted() {
    this.handleSubmit(0, 1);
    this.getDic();
  },
  data() {
    const checkChinese = (rule, value, callback) => {
      if (value) {
        if (/[^\d]/g.test(value)) {
          callback(new Error("任务编号只能为数字"));
        } else {
          callback();
        }
      }
      callback();
    };
    return {
      percentWidth:'40%',
      tableHeader:[
        {name:"序"},
        {name:"操作"},
        {name:"任务编号"},
        {name:"任务创建时间"},
        {name:"数据开始时间"},
        {name:"数据截止时间"},
        {name:"执行状态"},
        {name:"执行开始时间"},
        {name:"执行结束时间"},
      ],
      mapStatus:{
        0:"等待",
        1:"运行中",
        2:"运行成功",
        3:"运行失败",
      },
      tableBody:[],
      pageSize:10,
      pageNum:1,
      rowCount:0,
      currentpage:1,
      keyId:"",
      modelTitle:'强制更改',
      tabview: "",
      showModal: false,
      dataObj:{
        type:'1',//1新增 2 修改 3删除 4强制更改 5重新执行
      },
      stepNameList:[],//任务结果列表
      formValidate: {
        stepNum: "", //任务编号
        status:""
      },
      ruleValidate: {
        stepNum: [
          { required: false, validator: checkChinese, trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    /**
     * name:获取下拉字典
     */
    getDic(){
      this.axios
      .get("/system/getDic/STEP_STATUS")
      .then(response => {
        if (response.code == "200") {
          this.stepNameList = response.data.STEP_STATUS;
        }
      })
      .catch(function(error) {});
    },
    /**
     * name:查询方法
     * ifExacte:	 0 普通 1 精确
     * pageNo: 页数(1)
     * * */
    handleSubmit(ifExacte, pageNo){
      this.pageNum = pageNo;
      let data = {
        ifExacte:ifExacte,
        stepNum:this.formValidate.stepNum,
        status:this.formValidate.status,
        page:{
          pageNum:pageNo,
          pageSize:this.pageSize
        },
      }
      this.$refs.formValidate.validate(valid => {
        if(valid){
          this.axios
          .post("/system/taskStep/list", data, {
              headers: { "Content-Type": "application/json" }
            })
          .then(response => {
            if (response.code == "200") {
              this.tableBody = response.data.content;
              this.rowCount = response.data.totalSize;
            }
          })
          .catch(error => {});
        }
      })
    },
    pageSizefun(pageSize){
      this.pageSize = pageSize * 1;
      this.handleSubmit(0, 1);
    },
    pageChange(pageNo){
      this.pageNum = pageNo;
      this.handleSubmit(0, pageNo)
    },
   operate(type, item) {
      switch (type) {
        case "4":
          this.$Modal.confirm({
            title: "是否强制更改？",
            okText: "确认",
            cancelText: "取消",
            onOk: () => {
              this.forceUpdate(item);
            },
            onCancel: () => {}
          });
          break;
        case "5":
          this.$Modal.confirm({
            title: "是否重新执行？",
            okText: "确认",
            cancelText: "取消",
            onOk: () => {
              this.retryItem(item);
            },
            onCancel: () => {}
          });
          break;
      }
    },
    forceUpdate(item){
      this.axios
        .post(`/system/taskStep/focus/${item.step_num}`, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.$Message.success("更改成功！");
            this.handleSubmit(0, this.pageNum);
          }
        })
        .catch(error => {});
    },
    retryItem(item){
      console.log('item', item);
      if(item.task_id){
        this.axios
        .post(`/system/taskStep/retry/${item.task_id}`,{
            headers: { "Content-Type": "application/json" }
          })
          .then(response =>{
            if(response.code == '200'){
              this.$Message.success("执行成功！");
              this.handleSubmit(0, this.pageNum);
            }
          })
          .catch(error=>{
            console.log(error);
          })
      }
    },
    saveDatas() {
      this.showModal = false;
      this.handleSubmit(0,this.pageNum);
    },
    cancelDialog(val) {
      this.showModal = val;
      this.cancel();
    },
    cancel(){
      if(this.$refs['dialogModal'].$refs['formValidate']){
        this.$refs['dialogModal'].$refs['formValidate'].resetFields();
      }
    }
  }
};
</script>
<style scoped>
</style>



